<template>
    <div>
        <Head title="购物车"></Head>
        <h2>购物车....</h2>
        <div>
             <h2> vuex Demo </h2>
             <h2> count === {{$store.state.count}}  /  11==>  {{ count }} / {{count2}} / {{count3}} / {{count4}}</h2>
             <h2>city == {{city1}}  /  {{city2}}  // {{city}}</h2>
             <h2> newCount == {{ newCount }}</h2>
             <h2> msg == {{msgOne}}   / {{msg}}  </h2>   
             <p><input type="text" @input="getMsg($event)"></p>
            <div>
                <van-button type="default" @click="changeCount()">修改 count++ </van-button>
                <van-button type="primary" @click="countAdd(100)">countadd 100</van-button>
                <van-button type="warning" @click="countDesc(88)">count desc 88 </van-button>
                <van-button type="danger" @click="increment">count increment </van-button>
                <van-button type="danger" @click="decrement(90)">count decrement 90</van-button>
                <van-button type="info" @click="changeCity('我爱深圳...')">change city 我爱深圳...</van-button>
                <van-button type="info" @click="changeMsg()" >异步修改 msg </van-button>
            </div>
        </div>

    </div>
</template>

<script>
import store  from "@/store";
import {mapState,mapGetters,mapMutations,mapActions} from "vuex"
import {FIRSTTODO,COUNTADD,INCREMENT,DECREMENT} from "@/utils/mutationtype";

export default {
    data(){
        return {
            count1:this.$store.state.count,
            city1:this.$store.state.city
        }
    },
    // computed:{
    //     count2(){
    //         return this.$store.state.count
    //     },
    //     count3(){
    //         return this.$store.state.count + 1000;
    //     },
    //     city2(){
    //         return this.$store.state.city
    //     },
    //     newCount(){
    //         return this.$store.getters.newCount;
    //     },
    //     msgOne(){
    //         return this.$store.state.msg;
    //     }
    // },
    computed:{
        ...mapState({
            count2:state=>state.count,   //  箭头函数可使代码更简练
            count3:'count' ,  // 传字符串参数 'count' 等同于 `state => state.count`
            count4(state){
                return state.count + 200;
            },
            city2:'city'  ,
            msgOne:'msg'
        }),
        ...mapGetters([
            'newCount'   // 把 `this.newCount` 映射为 `this.$store.getters.newCount`
        ]),
        ...mapState([
            'count',
            'city',
            'msg'
            ])  // 映射 this.count 为 store.state.count
    },

    methods:{

        changeMsg(){
            // this.$axios.get("/vue/index").then(res=>{
            //     this.$store.commit("changeMsg",res.data);
            // })
            this.$store.dispatch("changeMsgAsync",{url:"/vue/index",params:{id:123456}})
        },
        getMsg(e){
            this.$store.commit("changeMsg",e.target.value);
        },
        // changeCount(){
        //     this.$store.commit("firstTodo");  
        // },
        // countAdd(){
        //     this.$store.commit("countAdd",10);
        // },
        // changeCity(city){
        //     this.$store.commit("changeCity",city);
        // },
        // countDesc(num){
        //     this.$store.commit("countDesc",num);
        // },
        // increment(){
        //     this.$store.dispatch("increment");
        // },
        // decrement(n){
        //     this.$store.dispatch("decrement",n);
        // },
        ...mapActions([
            INCREMENT,   // 将 `this.increment()` 映射为 `this.$store.dispatch('increment')`
            'decrement'
        ]),
        ...mapMutations({
            changeCount:'firstTodo'
        }),
        ...mapMutations([
            COUNTADD,  //  将 `this.countAdd(num)` 映射为 `this.$store.commit('countAdd', num)`
            'changeCity',
            'countDesc'
        ]),
    },
    mounted(){
        console.log(this.$store);
    }
}
</script>
